import React from 'react';
import Board from './Board'


function Game(props) {
    let field = {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '',
        7: '',
        8: '',
        9: ''
    };

    function checkWin(board) {
        const lines = [
            [1, 2, 3],
            [4, 5, 6],
            [7, 8, 9],
            [1, 4, 7],
            [2, 5, 8],
            [3, 6, 9],
            [1, 5, 9],
            [3, 5, 7]
        ];
        for (let i = 0; i < lines.length; i++) {
            const [a, b, c] = lines[i];
            if (board[a] && board[a] === board[b] && board[a] === board[c]) {
                props.ws.send(`GTE${props.enemy_id}&${props.game_id}`);
                props.setPanel({current: 'main', previous: ''});
                props.end();
            }
        }
    }

    return (
        <Board field={field} game_id={props.game_id} enemy_id={props.enemy_id} turn={props.turn}
               ws={props.ws} chip={props.chip} change={props.change} checkWin={checkWin}/>
    )
}

export default Game;